@import "../../common/styles/sass/variables";
@import "../../common/styles/sass/mixins";

.skill {
    width: 150px;
    @include flex(column, center, center, wrap);
    padding: 40px 20px 0 20px;
    .icon {
        height: 60px;
        width: 60px;
        img {
            height: 100%;
            width: 100%;
        }
    }
    .title {
        margin-top: 10px;
        @include text($titleFontFamily, 16px, 500)
    }
}
